import React from 'react';
import { Categoria } from '@/services/api/produtos.service';
import Button from '@/components/ui/Button';
import { PencilIcon, TrashBinIcon } from '@/icons';

type CategoryCardProps = {
  category: Categoria;
  onEdit: () => void;
  onDelete: () => void;
};

const CategoryCard: React.FC<CategoryCardProps> = ({ category, onEdit, onDelete }) => {
  return (
    <div className="relative overflow-hidden bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md transition-shadow shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-5">
      <div className="flex justify-between items-start">
        <div>
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
            {category.nome}
          </h3>
        </div>
        <div className="flex gap-2">
          <Button
            variant="secondary"
            size="xs"
            onClick={onEdit}
            title="Editar categoria"
              className="flex items-center justify-center text-gray-400 hover:text-brand-500 dark:hover:text-brand-400 transition-colors"
          >
            <PencilIcon className="h-5 w-5" />
          </Button>
          <Button
            variant="secondary"
            size="xs"
            onClick={onDelete}
            title="Excluir categoria"
            className="flex items-center justify-center text-red-500 dark:text-red-400 rounded-md transition-colors"
          >
            <TrashBinIcon className="h-5 w-5" />
          </Button>
        </div>
      </div>
    </div>
  );
};

export default CategoryCard;
