import React from 'react';
import { formatCurrency } from '@/utils/format';
import { Produto } from '@/services/api/produtos.service';
import { PencilIcon, TrashBinIcon } from '@/icons';
import Button from '../ui/Button';
import { useAuth } from '@/hooks/useAuth';

type ProductCardProps = {
  product: Produto;
  onEdit: () => void;
  onDelete: () => void;
};

const ProductCard: React.FC<ProductCardProps> = ({ product, onEdit, onDelete }) => {
  const { user: currentUser } = useAuth();
  console.log('ProductCard rendered with product:', product);
  return (
    <div 
    // className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700">
    className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div>
        <div className="flex justify-between items-start mb-3">
          <div>
            <h3 className="font-medium text-gray-900 dark:text-white">
              {product.nome}
            </h3>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              SKU: {product.sku}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Categoria: {product.categoria?.nome || 'Sem categoria'}
            </p>
          </div>
          <div className="flex gap-2">
          <Button
            variant="secondary"
            disabled={currentUser?.role.includes('ADMIN') ? false : true}
            size="xs"
              onClick={onEdit}
              className="flex items-center justify-center text-gray-400 hover:text-brand-500 dark:hover:text-brand-400 transition-colors"
            >
              <PencilIcon className="w-5 h-5" />
            </Button>
          <Button
            variant="secondary"
            disabled={currentUser?.role.includes('ADMIN') ? false : true}
            size="xs"
              onClick={onDelete}
            className="flex items-center justify-center text-red-500 dark:text-red-400 rounded-md transition-colors"
            >
              <TrashBinIcon className="w-5 h-5" />
            </Button>
          </div>
        </div>

        <div className="space-y-2">
          <p className="text-sm text-gray-600 dark:text-gray-300 line-clamp-2">
            {product.descricao}
          </p>

          <div className="flex justify-between items-center pt-2 border-t border-gray-100 dark:border-gray-700">
            <div className="text-sm">
              <span className="text-gray-500 dark:text-gray-400">Estoque: </span>
              <span className={`font-medium ${
                product.estoque > 10 
                  ? 'text-success-500' 
                  : product.estoque > 0 
                    ? 'text-warning-500' 
                    : 'text-error-500'
              }`}>
                {product.estoque} un
              </span>
            </div>
            <div>
              <span className="text-lg font-semibold text-brand-500 dark:text-brand-400">
                {formatCurrency(product.valor)}
              </span>
            </div>
          </div>

          {product.categoria && (
            <div className="pt-2 border-t border-gray-100 dark:border-gray-700">
              <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-gray-100 text-gray-800 dark:bg-gray-700 dark:text-gray-200">
                {product.categoria.nome}
              </span>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default ProductCard;
