"use client";

import React, { useState, useEffect, useCallback } from 'react';
import { services } from '@/services/api';
import { useAuth } from '@/hooks/useAuth';
import toast from 'react-hot-toast';
import Button from '@/components/ui/Button';
import SearchInput from '@/components/form/input/SearchInput';
import { Modal } from '@/components/ui/modal';
import ProductCard from '@/components/products/ProductCard';
import CategoryCard from '@/components/products/CategoryCard';
import ProductForm from '@/components/products/ProductForm';
import CategoryForm from '@/components/products/CategoryForm';
import Pagination from '@/components/ui/Pagination';
import Label from '@/components/form/Label';
import Input from '@/components/form/input/InputField';
import { Categoria, Produto, CreateProdutoDTO, CreateCategoriaDTO } from '@/services/api/produtos.service';

type View = 'PRODUTOS' | 'CATEGORIAS';

export default function ProdutosPage() {
  const { user: currentUser } = useAuth();
  const [products, setProducts] = useState<Produto[]>([]);
  const [categories, setCategories] = useState<Categoria[]>([]);
  const [loading, setLoading] = useState(true);
  const [loadingCategories, setLoadingCategories] = useState(true);
  const [loadingProducts, setLoadingProducts] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState<Produto | null>(null);
  const [selectedCategory, setSelectedCategory] = useState<Categoria | null>(null);
  const [activeView, setActiveView] = useState<View>('PRODUTOS');
  const [selectedCategoryFilter, setSelectedCategoryFilter] = useState<number | null>(null);
  const [itemsPerPage, setItemsPerPage] = useState(9);

  const loadCategories = useCallback(async () => {
    try {
      setLoadingCategories(true);
      const response = await services.categoria.list();
      setCategories(response.data.data);
      setLoadingCategories(false);
    } catch (error) {
      toast.error('Erro ao carregar categorias');
    } finally {
      setLoadingCategories(false);
    }
  }, []);

  const loadProducts = useCallback(async () => {
    try {
      setLoadingProducts(true);
      const response = await services.produto.list({
        page, 
        limit: itemsPerPage, 
        search: searchTerm
      });
      setProducts(response.data);
      setLoadingProducts(false);
      // Assumindo que a API retorna informações de paginação similar ao padrão
      if (response.pagination) {
        setTotalPages(response.pagination.pages);
      } else {
        // Fallback se não houver pagination information
        setTotalPages(Math.ceil((response.data?.length || 0) / itemsPerPage));
      }
    } catch (error) {
      toast.error('Erro ao carregar produtos');
    } finally {
      setLoadingProducts(false);
    }
  }, [page, searchTerm, itemsPerPage]);

  useEffect(() => {
    loadCategories();
  }, [loadCategories]);

  useEffect(() => {
    loadProducts();
  }, [loadProducts]);

  useEffect(() => {
    if (!loadingCategories && !loadingProducts) {
      setLoading(false);
    }
  }, [loadingCategories, loadingProducts]);

  const handleEdit = (product: Produto) => {
    setSelectedProduct(product);
    setIsModalOpen(true);
  };

  const handleDeleteProduct = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir este produto?')) return;

    try {
      await services.produto.delete(id);
      toast.success('Produto excluído com sucesso');
      loadProducts();
    } catch (error) {
      toast.error('Erro ao excluir produto');
    }
  };

  const handleDeleteCategory = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir esta categoria?')) return;

    try {
      await services.categoria.delete(id);
      toast.success('Categoria excluída com sucesso');
      loadProducts(); // Recarrega os produtos para atualizar as categorias
    } catch (error) {
      toast.error('Erro ao excluir categoria');
    }
  };

  const handleSaveProduct = async (data: Partial<Produto>) => {
    try {
      if (selectedProduct) {
        await services.produto.update(selectedProduct.id, data);
        toast.success('Produto atualizado com sucesso');
      } else {
        await services.produto.create(data as CreateProdutoDTO);
        toast.success('Produto criado com sucesso');
      }
      setIsModalOpen(false);
      setSelectedProduct(null);
      loadProducts();
    } catch (error) {
      toast.error('Erro ao salvar produto');
    }
  };

  const handleSaveCategory = async (data: CreateCategoriaDTO) => {
    try {
      if (selectedCategory) {
        await services.categoria.update(selectedCategory.id, data);
        toast.success('Categoria atualizada com sucesso');
      } else {
        await services.categoria.create(data);
        toast.success('Categoria criada com sucesso');
      }
      setIsModalOpen(false);
      setSelectedCategory(null);
      loadProducts(); // Recarrega os produtos para atualizar as categorias
      loadCategories(); // Recarrega as categorias
    } catch (error) {
      toast.error('Erro ao salvar categoria');
    }
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Produtos e Categorias
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie seus produtos e categorias
            </p>
          </div>
          <Button 
            variant="primary"
            disabled={currentUser?.role.includes('ADMIN') ? false : true}
            onClick={() => {
              setSelectedProduct(null);
              setIsModalOpen(true);
            }}
          >
            {activeView === 'PRODUTOS' ? 'Novo Produto' : 'Nova Categoria'}
          </Button>
        </div>

        <div className="mt-6">
          
          <div className={`flex flex-wrap gap-2 mb-6 ${currentUser?.role.includes('ADMIN') ? '' : 'pointer-events-none opacity-50'}`}>
            {['PRODUTOS', 'CATEGORIAS'].map((view) => (
              <button
                key={view}
                onClick={() => {
                  setActiveView(view as View);
                  if (view === 'CATEGORIAS') {
                    setSelectedCategoryFilter(null);
                  }
                }}
                className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors
                  ${activeView === view
                    ? 'bg-brand-500 text-white dark:bg-brand-400 dark:text-gray-900'
                    : 'bg-gray-100 text-gray-600 hover:bg-gray-200 dark:bg-gray-800 dark:text-gray-400 dark:hover:bg-gray-700'
                  }`}
              >
                {view === 'PRODUTOS' ? 'Produtos' : 'Categorias'}
              </button>
            ))}
          </div>

          <div className="flex flex-col sm:flex-row gap-4 items-center justify-between mb-6">
            <div className="flex gap-4 w-full sm:w-auto">
              <div className="w-full sm:w-96">
                <SearchInput
                  value={searchTerm}
                  onChange={(e) => {
                    setSearchTerm(e.target.value);
                    setPage(1);
                  }}
                  placeholder={`Buscar ${activeView === 'PRODUTOS' ? 'produtos' : 'categorias'}...`}
                />
              </div>
              {activeView === 'PRODUTOS' && (
                <select
                  title="Selecione uma categoria"
                  className="w-48 rounded-lg border border-gray-300 bg-white px-3 py-2 text-sm dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300"
                  onChange={(e) => {
                    const categoryId = e.target.value ? Number(e.target.value) : null;
                    setSelectedCategoryFilter(categoryId);
                    setPage(1);
                  }}
                  value={selectedCategoryFilter || ''}
                >
                  <option value="">Todas as categorias</option>
                  {categories.map((category) => (
                    <option key={category.id} value={category.id}>
                      {category.nome}
                    </option>
                  ))}
                </select>
              )}
            </div>
          </div>

          <div className="flex justify-between mb-4 gap-2 flex-wrap">
            <div className='flex items-center gap-0.5'>
              <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
              <Input
                className='max-w-24'
                defaultValue={itemsPerPage}
                max={100}
                min={1}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  const newValue = Number(e.target.value) || 9;
                  setItemsPerPage(newValue);
                  setPage(1);
                }}
              />
            </div>
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : activeView === 'PRODUTOS' ? (
        <>
          <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
            {products.length > 0 ? (
              products
                .filter(product => !selectedCategoryFilter || product.categoriaId === selectedCategoryFilter)
                .map((product) => (
                  <ProductCard
                    key={product.id}
                    product={product}
                    onEdit={() => handleEdit(product)}
                    onDelete={() => handleDeleteProduct(product.id)}
                  />
                ))
            ) : (
              <div className="col-span-full text-center py-12">
                <p className="text-gray-500 dark:text-gray-400">
                  Nenhum produto encontrado.
                </p>
              </div>
            )}
          </div>
        </>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {categories.length > 0 ? (
            categories.map((category) => (
              <CategoryCard
                key={category.id}
                category={category}
                onEdit={() => {
                  setSelectedCategory(category);
                  setIsModalOpen(true);
                }}
                onDelete={() => handleDeleteCategory(category.id)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhuma categoria encontrada.
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      )}

      <Modal 
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedProduct(null);
        }} 
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedProduct ? 'Editar Produto' : 'Novo Produto'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedProduct 
                ? 'Atualize as informações do produto.' 
                : 'Preencha os dados para criar um novo produto.'
              }
            </p>
          </div>
          {activeView === 'PRODUTOS' ? (
            <ProductForm
              product={selectedProduct || undefined}
              onSubmit={handleSaveProduct}
              onCancel={() => {
                setIsModalOpen(false);
                setSelectedProduct(null);
              }}
            />
          ) : (
            <CategoryForm
              category={selectedCategory || undefined}
              onSubmit={handleSaveCategory}
              onCancel={() => {
                setIsModalOpen(false);
                setSelectedProduct(null);
              }}
            />
          )}
        </div>
      </Modal>
    </div>
  );
}
