import React, { useState, useEffect } from 'react';
import { toast } from 'react-hot-toast';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Button from '@/components/ui/Button';
import { Produto, CreateProdutoDTO } from '@/services/api/produtos.service';
import { services } from '@/services/api';
import { formatCurrency, formatCurrencyInput, parseCurrencyInput } from '@/utils/format';
import Select from '../ui/Select';

type FormErrors = {
  nome?: string;
  sku?: string;
  descricao?: string;
  valor?: string;
  estoque?: string;
  categoriaId?: string;
};

type ProductFormProps = {
  product?: Produto;
  onSubmit: (data: CreateProdutoDTO) => Promise<void>;
  onCancel: () => void;
};

const ProductForm: React.FC<ProductFormProps> = ({ product, onSubmit, onCancel }) => {
  const [categorias, setCategorias] = useState<Array<{ id: number; nome: string }>>([]);
  const [formData, setFormData] = useState<CreateProdutoDTO>({
    nome: product?.nome || '',
    sku: product?.sku || '',
    descricao: product?.descricao || '',
    valor: product?.valor || 0,
    estoque: product?.estoque || 0,
    categoriaId: product?.categoriaId || 0,
  });
  const [valorFormatado, setValorFormatado] = useState(
    product?.valor ? formatCurrency(product.valor).replace('R$', '').trim() : ''
  );
  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    loadCategorias();
  }, []);

  const loadCategorias = async () => {
    try {
      const response = await services.categoria.list();
      setCategorias(response.data.data);
    } catch (error) {
      toast.error('Erro ao carregar categorias');
    }
  };

  const validateForm = () => {
    const newErrors: FormErrors = {};

    if (!formData.nome) {
      newErrors.nome = 'Nome é obrigatório';
    }

    if (!formData.sku) {
      newErrors.sku = 'SKU é obrigatório';
    }

    if (!formData.descricao) {
      newErrors.descricao = 'Descrição é obrigatória';
    }

    if (!formData.valor || formData.valor <= 0) {
      newErrors.valor = 'Valor deve ser maior que zero';
    }

    if (formData.estoque < 0) {
      newErrors.estoque = 'Estoque não pode ser negativo';
    }

    if (!formData.categoriaId) {
      newErrors.categoriaId = 'Categoria é obrigatória';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsSubmitting(true);
    try {
      await onSubmit(formData);
    } catch (error) {
      toast.error('Erro ao salvar produto');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="md:col-span-2">
          <Label htmlFor="nome">
            Nome<span className="text-error-500">*</span>
          </Label>
          <Input
            id="nome"
            name="nome"
            value={formData.nome}
            onChange={(e) => setFormData({ ...formData, nome: e.target.value })}
            error={errors.nome}
          />
        </div>

        <div>
          <Label htmlFor="sku">
            SKU<span className="text-error-500">*</span>
          </Label>
          <Input
            id="sku"
            name="sku"
            value={formData.sku}
            onChange={(e) => setFormData({ ...formData, sku: e.target.value })}
            error={errors.sku}
          />
        </div>

        <div>
          <Label htmlFor="categoriaId">
            Categoria<span className="text-error-500">*</span>
          </Label>

          <Select
            id="categoriaId"
            name="categoriaId"
            value={categorias.map(c => { return { value: c.id + '', label: c.nome } }).find(c => c.value === formData.categoriaId + '')}
            options={categorias.map(c => ({ value: c.id + '', label: c.nome }))}
            onChange={(option) => setFormData({ ...formData, categoriaId: Number((option as { value: string; label: string }).value) })}
            isSearchable
            placeholder="Selecione uma categoria"
            error={errors.categoriaId}
            className="w-full"
          />
        </div>

        <div className="md:col-span-2">
          <Label htmlFor="descricao">
            Descrição<span className="text-error-500">*</span>
          </Label>
          <textarea
            id="descricao"
            name="descricao"
            value={formData.descricao}
            onChange={(e) => setFormData({ ...formData, descricao: e.target.value })}
            rows={3}
            className="mt-1.5 w-full rounded-lg border border-gray-300 bg-white px-4 py-3 text-sm outline-none transition-all focus:border-brand-500 focus:ring-1 focus:ring-brand-500 dark:border-gray-800 dark:bg-gray-900"
          />
          {errors.descricao && (
            <span className="text-sm text-error-500">{errors.descricao}</span>
          )}
        </div>

        <div>
          <Label htmlFor="valor">
            Valor<span className="text-error-500">*</span>
          </Label>
          <div className="relative">
            <span className="absolute z-1 left-3 top-1/2 -translate-y-1/2 text-gray-500 dark:text-gray-300">
              R$
            </span>
            <Input
              id="valor"
              name="valor"
              type="text"
              className="pl-9"
              value={valorFormatado}
              onChange={(e) => {
                const input = e.target.value.replace(/\D/g, '');
                if (input === '') {
                  setValorFormatado('');
                  setFormData({ ...formData, valor: 0 });
                  return;
                }

                const formatted = formatCurrencyInput(input);
                setValorFormatado(formatted);
                setFormData({ ...formData, valor: parseCurrencyInput(input) });
              }}
              error={errors.valor}
            />
          </div>
        </div>

        <div>
          <Label htmlFor="estoque">
            Estoque<span className="text-error-500">*</span>
          </Label>
          <Input
            id="estoque"
            name="estoque"
            type="number"
            min="0"
            value={formData.estoque}
            onChange={(e) => setFormData({ ...formData, estoque: Number(e.target.value) })}
            error={errors.estoque}
          />
        </div>
      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          variant="secondary"
          onClick={onCancel}
          type="button"
        >
          Cancelar
        </Button>
        <Button
          variant="primary"
          type="submit"
          loading={isSubmitting}
        >
          {product ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default ProductForm;
